.tags {
  position: relative;
  margin: 0px 0px 10px 10px;
  height: 45px;
  overflow: hidden;
  background: #fff;
  padding-right: 120px;
  box-shadow: 10px 10px 10px 2px #ddd;
  box-sizing: border-box;
}
.tags ul {
  display: -webkit-box;
  display: flex;
  -webkit-box-align: center;
  align-items: center;
  box-sizing: border-box;
  width: 100%;
  height: 100%;
}
.tags-li {
  float: left;
  margin: 0px 5px 0px 3px;
  box-sizing: border-box;
  border-radius: 3px;
  font-size: 12px;
  overflow: hidden;
  cursor: pointer;
  height: 35px;
  line-height: 35px;
  border: 1px solid #e9eaec;
  background: #fff;
  padding: 0 10px 0 20px;
  vertical-align: middle;
  color: #666;
  -webkit-transition: all .3s ease-in;
  transition: all .3s ease-in;
}
.tags-li:not(.active):hover {
  background: #f8f8f8;
}
.tags-li.active {
  color: #fff;
}
.tags-li-title {
  float: left;
  max-width: 80px;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
  margin-right: 5px;
  color: #666;
}
.tags-li.active .tags-li-title {
  color: #fff;
}
.tags-close-box {
  position: absolute;
  display: -webkit-box;
  display: flex;
  -webkit-box-align: center;
  align-items: center;
  right: 0;
  top: 0;
  box-sizing: border-box;
  height: 100%;
  background: #fff;
  z-index: 10;
}
.tagsMenu {
  display: -webkit-box;
  display: flex;
  -webkit-box-align: center;
  align-items: center;
  box-sizing: border-box;
  margin-right: 3px;
  height: 35px;
  line-height: 30px;
}
